import React, { Component } from "react";
import "./styles.css";

class One extends Component {
  constructor() {
    super();
    this.state = {
      count: 0,
      color: "red"
    };
  }
  myclick = () => {
    this.setState({
      count: this.state.count + 2,
      color: "green"
    });
  };

  myclicks = () => {
    this.setState({
      count: this.state.count - 2
    });
  };

  render() {
    return (
      <div>
        <p style={{ color: this.state.color }} class="sty">
          {this.state.count}
        </p>{" "}
        <br />
        <button class="btn" onClick={this.myclick}>
          {"+"}
        </button>
        <button class="btn" onClick={this.myclicks}>
          {" "}
          -{" "}
        </button>
      </div>
    );
  }
}

export default One;
